<template>
  <div class="login-container">
    <div class="header">
      <div class="logo">LOGO</div>
    </div>
    <div class="main-content">
      <div class="login-form">
        <md-field>
          <md-input-item
            v-model="loginForm.username"
            ref="username"
            title="账号"
            placeholder="请输入您的账号"
            is-title-latent
            clearable
          />
          <md-input-item
            v-model="loginForm.password"
            ref="password"
            type="password"
            title="密码"
            placeholder="请输入6-16位密码(字母+数字组合)"
            is-title-latent
            clearable
          />
        </md-field>
        <md-button class="login-btn" @click="login">登 录</md-button>
      </div>
    </div>
    <div class="footer" v-if="isHidden">
      <div class="fl">
        <span class="spacing">借钱就找</span>, <span class="spacing">德聚金融</span>
      </div>
      <md-icon name="arrow-right" size="lg"></md-icon>
    </div>
  </div>
</template>

<script>
  import { Icon, InputItem, Field, Button, Toast} from 'mand-mobile'

  export default {
    name: 'LoginPage',
    /* eslint-disable */
    components: {
      [Icon.name]: Icon,
      [InputItem.name]: InputItem,
      [Field.name]: Field,
      [Button.name]: Button
    },
    data() {
      return {
        isHidden: true,
        screenHeight: 0,
        originHeight: 0,
        loginForm: {
          username: 'guest',
          password: '123456'
        }
      }
    },
    watch: {
      screenHeight(val) {
        if (this.originHeight != val) {
          this.isHidden = false
        } else {
          this.isHidden = true
        }
      }
    },
    mounted() {
      const self = this
      document.title = '登录'
      this.screenHeight = this.originHeight = document.body.clientHeight
      window.onresize = () => {
        return (() => {
          window.screenHeight = document.body.clientHeight
          self.screenHeight = window.screenHeight
        })()
      }
    },
    methods: {
      login() {
        if (!this.loginForm.username || !this.loginForm.password) {
          Toast.info('账号或密码不能为空哦~')
          return
        } else {
          Toast.loading('登录中')
          setTimeout(() => {
            Toast.hide()
            this.$router.push({ name: 'ProductList' })
          }, 1000)
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .login-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #fff;
  }

  .header,
  .main-content,
  .footer {
    width: 100%;
  }

  .header {
    height: 200px;
    position: relative;

    .logo {
      width: 200px;
      height: 100px;
      background-color: #ddd;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      font-size: 50px;
      line-height: 2;
      text-align: center;
    }
  }

  .main-content {
    flex: 1 auto;

    .login-btn {
      width: 92vw;
      margin: auto;
      margin-top: 40px;
    }
  }

  .footer {
    margin-bottom: 100px;
    font-size: 28px;
    color: #b4bfca;
    display: flex;
    justify-content: center;

    .fl {
      padding-top: 5px;
    }

    .spacing {
      letter-spacing: 5px;
    }
  }
</style>
